.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 150px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:white;
  
}
.card > .img-container > img {
  width: 100%;
  height:100%;
  cursor: pointer;
}

.img-thumbnail:hover {
  background-color: yellow;
  opacity: 0.7;
  }
.img-container:hover{
  transform: scale(1.1);
}